@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
.flex-grid{display:flex}.flex-grid .col.sidebar{width:20%;background:#1d1d1d;padding-bottom:0 !important}.flex-grid .col.main-content{width:80%;padding-bottom:30px !important}.flex-grid .col.sidebar,.flex-grid .col.main-content{padding:15px}

.cards-row{display:flex}.cards-row .card{flex:1 1;color:#ccc;text-align:center;text-transform:uppercase;font-size:14px;background:#6b6b6b;margin:10px;padding:10px;border:2px solid #6b6b6b;border-radius:50px}.cards-row .card span{display:block;color:#fff;font-size:24px;font-weight:600}.cards-row .card:hover{cursor:pointer}.cards-row .card:hover.confirmed{border:2px solid #e60036}.cards-row .card:hover.confirmed span{color:#e60036}.cards-row .card:hover.deaths{border:2px solid #571aab}.cards-row .card:hover.deaths span{color:#571aab}.cards-row .card:hover.recovered{border:2px solid #4fc974}.cards-row .card:hover.recovered span{color:#4fc974}.cards-row .card:hover.currently-sick{border:2px solid #33f}.cards-row .card:hover.currently-sick span{color:#33f}.input-search input{width:100%;margin:0 auto;font-size:16px;text-transform:capitalize;padding:5px 0;background:#1d1d1d;border:none;border-bottom:2px solid #ccc;color:white}.input-search input:focus{outline:transparent;color:white}.country-list{height:calc(100vh - 351px);overflow-y:scroll}.country-list::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(0,0,0,0.3);border-radius:10px;background-color:transparent}.country-list::-webkit-scrollbar{width:12px;background-color:transparent}.country-list::-webkit-scrollbar-thumb{border-radius:10px;box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:#6b6b6b}.country-list .country-row{border-bottom:1px solid #333}.country-list .country-row img{vertical-align:middle;margin-right:20px}.country-list .country-row p{display:inline-block}.country-list .country-row p:hover{cursor:pointer}.country-list .country-row .numbers{color:#e60036;font-weight:bold}

.main-content .text-in-center{font-size:32px;text-align:center;position:relative;top:50%;transform:translateY(-50%)}.main-content .info{display:flex;flex-wrap:wrap}.main-content .info .country-card{width:20%;box-sizing:border-box;text-align:center;text-transform:uppercase;color:#ccc;font-size:14px}.main-content .info .country-card span{display:block;font-weight:bold;margin-top:5px;color:#fff;font-size:22px}.main-content .chartjs-render-monitor{margin-top:30px}

body{margin:0;font-family:'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#333;color:#fff}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

